@font-face {
  font-family: CatCafe;
  src: url(fonts/CatCafe.ttf);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}
*, *:before, *:after {
  box-sizing: inherit;
}
::selection {
  background-color: orangered;
  color: #fff;
}

:root {
  --accent-color: #3D0B0B;
  --highlight-color:  #FDFAF4;
}

body {
  margin: 0;
  padding: 0;
  background-color: #fAf4e8;
  font-size: 1em;
  line-height: 1.1;
  color: var(--accent-color);
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

/* Search Bar */
.search-container {
  display: flex;
  justify-content: center;
  padding: 15px 30px 0;
  margin-bottom: 15px;
}

#homepage {
  height: calc(100vh - 205px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
}

#homepage h1 {
  font-family: 'CatCafe', 'Franklin Gothic Medium', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: clamp(3.5em, 5.5vw + 1em, 6.15em);
  text-align: center;
  margin: 15px auto 0;
}
#homepage div {
  max-width: 250px;
}
#homepage div img {
  width: 100%;
}

/* Results Page */
.results-options {
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: column-reverse;
  gap: 10px;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px 15px;
  margin: 0 auto;
  max-width: 1220px;
  z-index: 100;
  background-color: #fAf4e8;
}
.select-container {
  position: relative;
  display: flex;
  align-items: center;
}
.sort-by {
  padding: 10px 45px 10px 15px;
  border-radius: 65px;
  font-family: inherit;
  outline: none;
  appearance: none;
  background-color: #FDFAF4;
  color: inherit;
  border: 0.75px solid var(--accent-color);
}
.sort-by option {
  padding: 10px;
  background-color: var(--highlight-color);
  color: inherit;
}
.sort-by::part(listbox) {
  padding: 10px;
}
.select-container div {
  position: absolute;
  top: 0;
  right: 12px;
  display: block;
  height: 100%;
  width: 20px;
  border-radius: 65px;
  display: flex;
  align-items: center;
  pointer-events: none;
}
.select-container div img {
  width: 20px;
}

/* Gallery */
.gallery-container {
  padding: 15px 30px 0;
  margin: 0 auto 60px;
  max-width: 1420px;
}

/* About Page */
#about-page {
  padding: 30px 30px 60px;
  margin: auto;
  max-width: 1000px;
  width: 100%;
  text-align: center;
}
#about-page p {
  max-width: 800px;
  margin: auto;
  font-size: 1.25em;
  line-height: 1.35;
}
.hero-image {
  width: 100%;
  display: flex;
  justify-content: center;
}
.about-section {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  width: 75%;
  margin: auto;
}
#about-page .first {
  flex-direction: column;
}
.about-title {
  font-family: 'CatCafe', 'Franklin Gothic Medium', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: clamp(3.5em, 5.5vw + 1em, 5.5em);
}
.about-section h2 {
  font-family: 'CatCafe', 'Franklin Gothic Medium', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: clamp(2.25em, 3vw + 1em, 3.5em);
}
.about-section div {
  display: flex;
  justify-content: center;
}
#about-page img {
  width: 100%;
}
.about-item {
  flex-direction: column;
}
.about-item p span {
  padding: 5px 10px;
  border-radius: 65px;
  background-color: var(--accent-color);
}
.about-item p span img {
  width: 1em !important;
}

/* Footer */
footer {
  background-color: #E9E1D0;
  height: 100px;
  padding: 15px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer a {
  padding: 5px 5px 0.5em;
  text-decoration: none;
  color: inherit;
  font-family: 'CatCafe', 'Franklin Gothic Medium', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 600;
}
footer p {
  margin: 0;
}

@media (min-width: 600px) {
    /* Results Page */
  .results-options {
    flex-direction: row;
  }

  /* Gallery */
  .gallery-container {
    padding: 15px 30px 0;
    margin: 0 auto 60px;
    max-width: 1420px;
  }
  .about-section {
    flex-direction: row !important;
  }
}